.homeContent{
    height: 520px;
    width: 100%;
    background-color: rgb(32, 29, 29);
    display: flex;
    justify-content: center;
    margin-bottom: 0px;
}
.homeContent img{
    margin-right: 150px;
    margin-top: 80px;
    height: 350px;
    width: 350px;
    border-radius: 200px;
}
.homeContent img:hover{
    box-shadow: 0px 0px 15px rgb(213, 244, 16);
}
.content h1{
    color: whitesmoke;
    margin-top: 100px;
}
.content p{
    color: whitesmoke;
    line-height: 2.2;
    font-size: larger;
}