.page{
    width: 100%;
    height: 75px;
    background-color: rgb(1, 1, 1);
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.title{
    flex:1;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 30px;
    color: rgb(253, 0, 0);
    margin-left: 50px;
}
.subtopics{
    display: flex; 
    justify-content: flex-end; 
    gap: 65px; 
    white-space: nowrap;
    overflow: hidden; 
    margin-right: 55px;
}
.subtopics span{
    color: whitesmoke;
    font-size: 18px;
    font-family: cursive;
}