.contactpage{
    width: 100%;
    height: 550px;
    background-color: rgb(2, 2, 2);
    display: flex;
}
.icons{
    color: white;
    margin-top: 180px;
    margin-left: 150px;
}
.icons h1{
    font-family: cursive;
}
.icons img{
    height: 45px;
    width: 45px;
}
.forms{
    color: white;
    margin-top: 60px;
    height: 430px;
    width: 600px;
    border: 2px solid rgb(250, 249, 249);
    margin-left: 380px;
    line-height: 2.3;
}
.forms form,h2{
    margin-left: 45px;
}
.forms input{
    border: none;
    font-size: 15px;
    width: 480px;
    height: 35px;
    border-radius: 15px;
    border: 1px 1px;
    padding-left: 20px;
}
.forms button{
    height: 38px;
    width: 140px;
    margin-left: 190px;
    margin-top: 25px;
    border-radius: 15px;
    font-size: 17px;
}
.forms button:hover{
    color: white;
    background-color: red;
    box-shadow: 0px 0px 10px white;
    border: none;
}